.skills {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.skillgroup {
  font-family: 'Press Start 2P', cursive;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 5px;
  padding-top: 30px;
  padding-bottom: 45px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  color: white;
  border-radius: 10px 10%;
  width: 40%;
}
.skillgroup:nth-child(1) {
  background-color: #07338C;
}

.skillgroup:nth-child(2){
  background-color: #307CBF;
}

.skillgroup:nth-child(3){
  background-color: #3d5afe;
}
.skillgroup:last-child {
  background-color: #00bfa5;
}

.skill {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 2px;
  border: 2px solid #EF6C00;
  height: 50px;
  width: 50px;
  border-radius: 10px 5%;
}

.skill-name {
  display:none;
  font-size: 7px;
}

.skill-svg {
  width: 100%;
}

.skill-label {
  font-size: x-small;
  text-align: left;
  padding-left: 12px;
  padding-bottom: 4px;
  -ms-flex: 0 0 100%;
      flex: 0 0 100%;
}



